:root {
  --bg-color: #0e0e0e;
  --disabled-color: #202020;
  --green-color-dim: #133218;
  --green-color: #4ac45a;
  --neutral-color: grey;
  --red-color-dim: #230000;
  --red-color: darkred;
}

@font-face {
  font-family: PixelifySans;
  src: url("PixelifySans-VariableFont-wght.fb2c1bff.ttf");
  font-display: swap;
}

body {
  background-color: var(--bg-color);
  color: var(--green-color);
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  font-family: PixelifySans, Helvetica, sans-serif;
  display: flex;
}

header, footer {
  opacity: .5;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  display: flex;
  overflow: hidden;
}

header h1, footer h1 {
  margin: 0;
  font-size: 5em;
}

header img, footer img {
  height: 60px;
}

.title.red {
  color: var(--red-color);
}

main {
  border: 1px solid var(--green-color);
  width: 50vw;
  max-width: 500px;
  min-height: 35vh;
  box-shadow: 0px 0px 30px var(--green-color);
  border-radius: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  padding: 48px;
  display: flex;
}

main.red {
  border: 1px solid var(--red-color);
  box-shadow: 0px 0px 30px var(--red-color);
}

button {
  cursor: pointer;
  border-radius: 12px;
  min-height: 40px;
  padding: 12px;
}

#heading {
  text-align: center;
  width: 100%;
  margin: 0;
  font-weight: 100;
}

#tutorial-section {
  color: var(--neutral-color);
  line-height: 150%;
}

#tutorial-section ol {
  margin-block-start: 0;
}

#select-difficulty {
  justify-content: center;
  gap: 6px;
  display: flex;
}

#select-difficulty button {
  background-color: var(--bg-color);
  color: var(--neutral-color);
  border: 1px solid var(--neutral-color);
  font-family: inherit;
  font-size: 1em;
}

#select-difficulty button.selected {
  border-color: var(--green-color);
  color: var(--green-color);
}

#answers-section {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  width: 100%;
  display: grid;
}

#answers-section button {
  background-color: var(--bg-color);
  width: 100%;
  height: 100%;
  color: var(--neutral-color);
  border: 1px solid var(--neutral-color);
  font-family: inherit;
}

#answers-section button.disabled {
  cursor: auto;
}

#answers-section button:hover {
  background-color: var(--disabled-color);
}

#answers-section button.disabled:hover {
  background-color: var(--bg-color);
}

#answers-section button.correct {
  border-color: var(--green-color);
  color: var(--green-color);
}

#answers-section button.incorrect {
  border-color: var(--red-color);
  color: var(--red-color);
}

#answers-section button.correct.selected {
  background-color: var(--green-color-dim);
}

#answers-section button.incorrect.selected {
  background-color: var(--red-color-dim);
}

#results-section {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 50%;
  display: flex;
}

#results-section #score {
  font-size: 3em;
}

#submit-button {
  background-color: var(--green-color);
  color: var(--bg-color);
  border: none;
  width: 200px;
  font-family: inherit;
  font-size: 1.1em;
}

#submit-button.disabled {
  background-color: var(--disabled-color);
}

.flip-text {
  -webkit-transform: rotateY(180deg);
  -moz-transform: rotateY(180deg);
  -ms-transform: rotateY(180deg);
  -o-transform: rotateY(180deg);
}

.hidden {
  display: none !important;
}

@media screen and (width <= 768px) {
  body {
    justify-content: space-evenly;
  }

  main {
    justify-content: space-between;
    width: 70vw;
    min-height: 60vh;
    max-height: 75vh;
    padding: 36px;
  }

  header {
    height: 10vh;
  }

  header h1, footer h1 {
    font-size: 2em;
  }

  header .symbol, footer .symbol {
    max-height: 6vh;
  }

  #heading.shrink {
    font-size: 1.4em;
  }

  #tutorial-section ol {
    margin-bottom: 24px;
    padding-inline-start: 20px;
  }

  #select-difficulty {
    flex-direction: column;
  }

  #answers-section {
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr;
  }

  #answers-section button {
    font-size: 1em;
  }

  #submit-button {
    width: 100%;
  }
}

@media screen and (width <= 1024px) and (height <= 768px) and (orientation: landscape) {
  body {
    flex-direction: row;
  }

  main {
    width: 70vw;
    max-height: none;
    padding: 36px;
  }

  header, footer {
    width: 20%;
    transform: rotate(-90deg);
  }

  header h1, footer h1 {
    text-align: center;
    font-size: 2em;
  }

  header h1.title, footer h1.title {
    white-space: nowrap;
    margin: 6px;
  }

  .title {
    display: none;
  }

  #heading.shrink {
    font-size: 1.4em;
  }
}

@media screen and (height <= 600px) and (orientation: portrait) {
  body {
    justify-content: flex-end;
    height: auto;
  }

  main {
    border-width: 0;
    border-radius: 24px 24px 0 0;
    width: 80vw;
    height: 80vh;
    max-height: 80vh;
    padding: 36px;
  }

  header {
    height: 10vh;
  }

  footer {
    display: none;
  }
}

@media screen and (width <= 600px) and (orientation: landscape) {
  body {
    flex-direction: column;
    height: auto;
  }

  main {
    width: 70vw;
    height: auto;
    max-height: none;
    margin: 5vh 0;
  }

  header {
    width: 100%;
    height: 20vh;
    transform: none;
  }

  header .title {
    display: block;
  }

  header, footer {
    display: none;
  }

  #heading {
    font-size: 1.4em;
  }

  #heading.shrink {
    font-size: 1.3em;
  }

  #select-difficulty {
    flex-direction: row;
  }

  #answers-section {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
  }
}
